___CSS_LOADER_EXPORT___.push([module.id, `.Switch-module__switch--aYbzT {
    position: relative;
    display: inline-block;
    width: 50px;
    height: 28px;
}

.Switch-module__switch--aYbzT input {
    opacity: 0;
    width: 0;
    height: 0;
}

.Switch-module__slider--e1cHP {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    border-radius: 28px;
    transition: 0.4s;
}

/* сам "ползунок" */
.Switch-module__slider--e1cHP:before {
    position: absolute;
    content: "";
    height: 22px;
    width: 22px;
    left: 3px;
    bottom: 3px;
    background-color: white;
    border-radius: 50%;
    transition: 0.4s;
}

input:checked + .Switch-module__slider--e1cHP {
    background-color: #2196F3;
}

input:checked + .Switch-module__slider--e1cHP:before {
    transform: translateX(22px);
}

